import Bmob from "hydrogen-js-sdk";
let that;
export default {
	components: {},
	data() {
		return {
			user: null,
		};
	},
	onLoad() {
		uni.setNavigationBarTitle({"title":"系统设置"})
		that = this;
	},
	onShow() {
		that.user = uni.getStorageSync("user");
	},
	methods: {
		
		systemStart(){
			uni.showModal({
				title: '提示',
				content: '是否进行系统重建，除账号信息外其他信息将被清除',
				success: function(res) {
					if (res.confirm) {
						let params = {
							funcName: 'accountStart',
							data: {
								uid: that.user.objectId,
							}
						}
						Bmob.functions(params.funcName, params.data).then(function(res) {
							uni.showToast({
								icon: "none",
								title: res.msg
							})
						})
					}
				}
			});
		},
		
		accountDelete() {
			uni.showModal({
				title: '提示',
				content: '是否确认注销当前账户',
				success: function(res) {
					if (res.confirm) {
						let params = {
							funcName: 'accountDelete',
							data: {
								uid: that.user.objectId,
							}
						}
						Bmob.functions(params.funcName, params.data).then(function(res) {
							uni.showToast({
								icon: "none",
								title: res.msg
							})
						})
					}
				}
			});
		},
		
		copyAllFile(){
			uni.showModal({
				title: '提示',
				content: '是否备份当前账户的产品数据',
				success: function(res) {
					if (res.confirm) {
						let params = {
							funcName: 'copayAllFile',
							data: {
								uid: that.user.objectId,
							}
						}
						Bmob.functions(params.funcName, params.data).then(function(res) {
							uni.showToast({
								icon: "none",
								title: res.msg
							})
						})
					}
				}
			});
		},
	}
}
